<template>
  <div id="myinfo">
    <p class="title">管理员信息</p>
    <div class="main">
      <!-- <div v-for="item in myInfos" :key="item.title">
            {{}}
      </div>-->

      <el-form :label-position="labelPosition" label-width="120px">
        <el-form-item class="head" label="管理员头像:">
          <div class="uphead">
            <img :src="myInfos.imgUrl" />
            <!-- 上传头像插件 -->
            <div id="box1">
              <el-upload
                id="box"
                class="avatar-uploader"
                action="http://127.0.0.1:5000/users/avatar_upload"
                :show-file-list="false"
                :data="data"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
          </div>
        </el-form-item>

        <el-form-item label="管理员ID:">
          <span>{{myInfos.id}}</span>
        </el-form-item>

        <el-form-item label="账号:">
          <span>{{myInfos.account}}</span>
        </el-form-item>

        <el-form-item label="用户组:">
          <span class="userGroup">{{myInfos.userGroup}}</span>
        </el-form-item>

        <el-form-item label="注册时间:">
          <span>{{myInfos.ctime}}</span>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
// 引入接口
import { API_GET_MYINFO } from "@/API/apis.js";
export default {
  data() {
    return {
      // 我的信息数组
      myInfos: [
        {
          id: "",
          ctime: "",
          account: "",
          userGroup: "",
          imgUrl: ""
        }
      ],
      // 表单对齐方式
      labelPosition: "left",
      imageUrl: "",
      data: { id: localStorage.id }
    };
  },
  methods: {
    handleAvatarSuccess() {
      window.location.reload()
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  },
  //   vue钩子函数，created阶段
  created() {
    //   在data，组件，事件初始化完成后，获取页面数据
    // 获取当前用户id，用作参数。 id在本地存储获取
    let userId = localStorage.getItem("id");
    // 发送请求
    API_GET_MYINFO(userId).then(res => {
      console.log(res);
      // 把数据保存到data中
      if (res.statusText == "OK") {
        this.myInfos = res.data.accountInfo;
        localStorage.head=this.myInfos.imgUrl
      }
      this.myInfos.ctime = this.myInfos.ctime.slice(0, 10);
      console.log(this.myInfos.ctime);
      // for (let i of this.myInfos) {
      //   i.ctime = i.ctime.slice(0, 10); //截取时间（只保留年月日）
      //   console.log(i)
      //  }
    });
  }
};
</script>

<style lang="less" scoped>
#box1 {
  width: 102px;
  height: 102px;
}
#myinfo {
  width: 100%;
  background-color: #fff;
  border-radius: 8px;
  padding-top: 10px;
  box-shadow: 0 1px 6px 2px #ccc;
  .title {
    border-bottom: 1px solid #ccc;
    text-indent: 10px;
    line-height: 35px;
    margin: 0;
  }
  .main{
    padding-left: 10px;
  }
  #box {
    width: 100px;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  #box:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
  .userGroup {
    display: inline-block;
    background-color: rgb(38, 120, 226);
    color: #fff;
    height: 30px;
    line-height: 30px;
    border-radius: 5px;
    padding: 0 3px;
    text-indent: 0;
  }
  .edithead {
    padding-left: 10px;
    cursor: pointer;
  }

  span {
    color: rgb(0, 104, 240);
  }
  div:not(last-type-of-child) {
    border-bottom: 1px solid #f2f2f2;
    margin: 0px;
    line-height: 50px;
  }
  .head {
    margin-top: 30px;
    padding-bottom: 10px;
    .uphead {
      display: flex;
      border: none;
    }
    img {
      width: 100px;
      height: 100px;
      background-color: #ccc;
      display: inline-block;
      -vertical-align: middle;
      border: 1px solid #ccc;
      margin-right: 10px;
    }
  }
}
</style>